body,html{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a{
    text-decoration: none;
}
a:hover{
    text-decoration: none;
}

[ng-controller="systemCenterCtrl"]{
  height: 100%;
}
//登录页样式
#myBanner{
    display: block;
    width: 100%;
    height: calc(~"100% - 60px");
    .backImg{
        width: 100%;
        height: 100%;
    }
    .title{
        position: absolute;
        z-index: 100;
        color: #fff;
        width: 100%;
        height: 125px;
        top: 5%;
        text-align: center;
        .logBox{
            display: inline-block;
            width:120px;
            height: 120px;
            img{
                display: block;
                width: 100%;
                height: auto;
                padding-top: 26px;
            }
        }
        .titleText{
            display: inline-block;
            p:nth-of-type(1){
                font: .4rem/.5rem "微软雅黑";
                text-shadow: 0 2px 5px #000;
                margin: 0 0 5px 0;
            }
            p:nth-of-type(2){
                font: .16rem/.2rem "微软雅黑";
                opacity: .6;
                filter: alpha(opacity=60);
            }
        }
    }
    .logOut{
        position: absolute;
        right: 0;
        top: 5px;
        z-index: 100;
        cursor: pointer;
        .userImg{
            width: 35px;
            height: 35px;
            margin-right: 18px;
        }
        .userName{
            margin-right: 6px; 
            color: #fff;
        }
        .icon-chevron-down{
            color: #fff;
            margin-right: 10px;
        }
        .message{
            padding-left: 0;
            margin-top: 10px;
            list-style: none;
            li{
                line-height: 20px;
                text-align: center;
            }
        }
    }
}
